import React from 'react';
import Table from 'react-bootstrap/Table';

class ItemList extends React.Component {
    createTable = () => {
        const { items } = this.props;
        let table = []
        let body = [];
        table.push(<thead>
            <tr>
            <th>#</th>
            <th>Nombre</th>
            <th>Acciones</th>
            </tr>
        </thead>);

        items.forEach(item => {
            let tr = [];
            let td = [];
            td.push(<td>1</td>)
            td.push(<td>{item}</td>)
            td.push(<td>Acciones</td>)
            tr.push(<tr>{td}</tr>);
            body.push(tr);
        });

        table.push(<tbody>{body}</tbody>);

        return table
    }

    render() {
        return (
            <Table striped bordered hover>
                {this.createTable()}
            </Table>
        )
    }
}

export default ItemList;


