import React from 'react';
import ItemList from './ItemList';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import axios from 'axios';

class CourseType extends React.Component {
    state = {
        show: false,
        newCategory: '',
        saveDisabled: true,
        items: ["Idioma", "Tecnologia"] 
    }

    handleClick = (e) => {
        this.setState({ 
            show: true 
          });
      
          if (e) {
            e.preventDefault();
          }
    }

    getDataAxios = async () =>{
        /*const response =
          await axios.get("http://localhost:4567/shirts");*/
        console.log("HOLA");
    }

    handleClose = (e) => {
        this.setState({
            show: false
        });
        
        if (e) {
            e.preventDefault();
        }
    }

    handleOnChange = (e) => {
        if (e.target.value === '') {
            this.setState({
                saveDisabled: true
            });
        } else {
            this.setState({
                newCategory: e.target.value,
                saveDisabled: false
            });
        }
    }

    handleSave = async (e) => {
        console.log("HOLA");
        let newItems = this.state.items;
        newItems.push(this.state.newCategory);
        this.setState({
            items: newItems
        });
        this.setState({
            show: false
        });
        if (e) {
            e.preventDefault();
        }
    }

    

    render() {
        return (
            <div>
                <p>Tipos de Cursos de Capacitacion</p> <button className="btn btn-info" onClick={this.handleClick}>Agregar</button>
                <ItemList items={this.state.items}/>
                <Modal show={this.state.show} onHide={this.handleClose}>
                    <Modal.Header closeButton>
                        <Modal.Title>Nueva Categoria</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        Categoria <input type="text" onChange={this.handleOnChange}/>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button variant="secondary" onClick={this.handleClose}>
                        Cancelar
                        </Button>
                        <Button variant="primary" onClick={this.handleSave} disabled={this.state.saveDisabled}>
                        Guardar
                        </Button>
                    </Modal.Footer>
                </Modal>
            </div>

        )
    }
}

export default CourseType;


